import React, { useState, useEffect } from "react";
import "../assets/css/Tempapp.css";
import { TextField, Typography } from "@material-ui/core";
import StreetviewIcon from "@material-ui/icons/Streetview";
import { data, event } from "jquery";
var randomCountryName = require("random-country-name");
let $ = require("jquery");
let geolocator = require("geolocator");

const Tempapp = () => {
  const [city, setCity] = useState(null);
  const [search, setSearch] = useState(null);

  useEffect(() => {
    const fetchApi = async () => {
      const url = `https://api.openweathermap.org/data/2.5/weather?q=${search}&units=metric&appid=ef6b1e0d20159187426be8a35838f92e`;

      const response = await fetch(url);
      if (response.status == 404) {
        console.log("Not found.");
      } else {
        const resJson = await response.json();
        //console.log(resJson);
        setCity(resJson.main);
      }
    };
    fetchApi();
  }, [search]);

  var options = {
    enableHighAccuracy: true,
    timeout: 5000,
            maximumWait: 10000,     // max wait time for desired accuracy
            maximumAge: 0,          // disable cache
            desiredAccuracy: 30,    // meters
    fallbackToIP: true, // fallback to IP if Geolocation fails or rejected
  };




  return (
    <>
      <div className="box">
        <div className="inputData">
          <input
            type="search"
            id="s"
            className="inputFeild"
            onChange={(event) => {
              setSearch(event.target.value);
            }}
          />
        </div>

        {!city ? (
          <div className="info">
            <Typography variant="h2" className="location">
              <StreetviewIcon
                className="fas fa-street-view"
                style={{ fontSize: "68px" }}
              />
              {randomCountryName.random()}
            </Typography>
            <Typography variant="h1" className="temp">
              1° Cel
            </Typography>
            <Typography variant="h3" className="tempmin_max">
              Min : 21° Cel | Max : 213° Cel
            </Typography>
          </div>
        ) : (
          <div>
            <div className="info">
              <Typography variant="h2" className="location">
                <StreetviewIcon
                  className="fas fa-street-view"
                  style={{ fontSize: "68px" }}
                />
                {search}
              </Typography>
              <Typography variant="h1" className="temp">
                {city.temp}° Cel
              </Typography>
              <Typography variant="h3" className="tempmin_max">
                Min : {city.temp_min}° Cel | Max : {city.temp_max}° Cel
              </Typography>
            </div>

            {/*<div className="wave -one"></div>
            <div className="wave -two"></div>
        <div className="wave -three"></div>*/}
          </div>
        )}
      </div>
    </>
  );
};

export default Tempapp;
